* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
}
.main-box {
  width: 1100px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 10px auto;
}
.banner {
  padding: 5px 0;
  display: flex;
  flex-wrap: wrap;
  border-top: 10px solid rgb(66, 212, 53);
}
.banner > div:first-child {
  width: 85%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 162, 0);
}
.banner > div:first-child > div:first-child {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  object-fit: cover;
  background-color: white;
  padding: 15px;
  margin: 15px 20px;
}
.banner img {
  width: 100%;
}
h1 {
  width: 50%;
  font-size: 70px;
  letter-spacing: 1px;
  color: white;
  font-family: 'Paytone One', sans-serif;
}
.banner > div:last-child {
  width: 15%;
  padding: 5px 5px 5px 5px;
  background-color: rgb(66, 212, 53);
  color: white;
  font-weight: 500;
  font-size: 18px;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  border-left: 5px solid white;
}
.banner > div:last-child > div:nth-child(1) {
  font-size: 26px;
  font-weight: 300;
  line-height: 28px;
}
h2 {
  width: 100%;
  font-family: 'Poppins', sans-serif;
  font-size: 24px;
  color: rgb(22, 179, 8);
  text-align: center;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid rgb(66, 212, 53);
}
.login-div {
  width: 300px;
  padding: 10px 5px 10px 10px;
}
.login-div > div:first-child {
  padding: 15px 10px;
  /* border: 3px solid rgb(228, 228, 228); */
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
}
.login-div > div:first-child > div {
  font-size: 12px;
  text-transform: uppercase;
}
.login-div > div:first-child > .error-msg {
  text-transform: inherit;
  color: rgb(255, 51, 0);
}
.login-div input {
  width: 100%;
  margin: 5px 0 10px 0;
  padding: 5px;
  outline: 1px solid rgb(151, 231, 143);
  border: none;
}
.login-div button {
  width: 100%;
  margin-top: 5px;
  padding: 4px 10px 5px 10px;
  border-radius: 3px;
  border: 0;
  background-color: rgb(255, 162, 0);
  color: white;
}
.login-div button:hover {
  background-color: rgb(255, 115, 0);
}
.login-div button:active {
  background-color: rgb(255, 51, 0);
}
.login-div p {
  font-weight: 700;
  color: rgb(255, 115, 0);
  font-size: 20px;
}
.login-div h3 {
  font-size: 30px;
  text-align: center;
  color: rgb(255, 115, 0);
}
.navigation {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: rgb(66, 212, 53);
}
.navigation button {
  font-size: 16px;
  font-weight: 500;
  padding: 10px 15px;
  border: none;
  background-color: rgb(66, 212, 53);
  color: white;
}
.navigation button:hover {
  cursor: pointer;
  text-decoration: underline;
}
.navigation > div:last-child {
  display: flex;
  align-items: center;
}
.navigation > div:last-child > div:first-child {
  color: white;
  font-weight: 500;
  margin-right: 15px;
}
.navigation > div:last-child > button {
  background-color: rgb(255, 162, 0);
}
.body-div {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  background-color: rgb(255, 254, 169);
}
.body-div > div {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.main-box > div:first-child {
  width: 100%;
}
.select-div {
  width: 100%;
  text-align: center;
}
.select-div select {
  width: 30%;
  font-size: 16px;
  margin: 5px auto;
  margin-top: 0;
  padding: 4px 10px;
}
.menu-container {
  width: 660px;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-content: flex-start;
  margin: 10px 5px;
  padding: 15px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}
.menu-item-box {
  width: 32.5%;
  margin: 3px 2.6px;
  padding: 5px;
  border-radius: 10px;
  background-color: white;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  border: 2px solid rgb(151, 231, 143);
}
.menu-item-box > div:first-child {
  width: 35%;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-item-box > div:first-child img {
  width: 75px;
  object-fit: cover;
}
.menu-item-box > div:nth-child(2) {
  padding: 5px 5px 5px 0;
  display: flex;
  width: 59%;
  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
}
.menu-item-box > div:nth-child(2) > p:first-child {
  text-transform: uppercase;
  font-weight: bolder;
  font-size: 15px;
  margin: 3px 0;
  padding-bottom: 3px;
  color: rgb(255, 115, 0);
  border-bottom: 1px solid rgb(66, 212, 53);
}
.menu-item-box > div:nth-child(2) > p:nth-child(2) {
  font-size: 12px;
}
p {
  width: 100%;
  text-align: center;
  font-size: 13px;
}
.menu-item-box .order-btn {
  margin: 5px 0 0 0;
  padding: 0 10px;
  height: 25px;
  border-radius: 3px;
  border: 0;
  background-color: rgb(66, 212, 53);
  color: white;
  font-size: 12px;
}
.menu-item-box .order-btn:hover {
  background-color: rgb(255, 115, 0);
  padding: 0 10px;
}
.menu-item-box .own-pizza-btn:disabled {
  background-color: rgb(218, 218, 218);
  color: rgb(100, 100, 100);
}
.btn-disabled-true {
  opacity: 35%;
}
.admin-btns {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-item-box span {
  height: 20px;
  font-size: 10px;
  text-transform: uppercase;
  padding: 3px 5px;
  margin-right: 8px;
  border-radius: 5px;
  display: flex;
  align-items: center;
}
.Food span {
  background-color: rgb(255, 254, 169);
}
.Drink span {
  background-color: rgb(181, 252, 175);
}
.menu-item-box .crud-btn {
  width: 22px;
  height: 22px;
  overflow: hidden;
  border: none;
  background-color: white;
  margin: 5px;
}
.menu-item-box .crud-btn img {
  width: 100%;
  object-fit: cover;
}
.menu-item-box .crud-btn:hover {
  box-shadow: 3px 3px rgb(255, 252, 99);
}
.cart {
  width: 410px;
  font-size: 14px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  margin: 10px 5px;
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}
.cart h2 {
  margin-bottom: 0;
}
.cart > div {
  width: 100%;
}
.cart > div:first-child > .pizza-name {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.pizza-name > div:first-child {
  font-size: 16px;
  color: rgb(22, 179, 8);
  font-weight: 600;
}
.pizza-name input {
  width: 250px;
  padding: 3px;
  text-align: center;
  font-weight: 600;
  color: rgb(255, 115, 0);
  font-size: 16px;
  outline: none;
  border: 1px solid rgb(151, 231, 143);
}
.cart-details {
  width: 100%;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
}
.cart-table {
  width: 100%;
  height: 75%;
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
}
.cart-item-row {
  width: 100%;
  font-size: 14px;
  display: flex;
  flex-flow: row wrap;
  margin: 7px 0;
  padding: 0 5px 0 15px;
}
.cart-item-row > div:first-child {
  width: 39%;
  display: flex;
  justify-content: space-between;
}
.cart-item-row > div:first-child button {
  width: 20px;
  height: 20px;
  overflow: hidden;
  border: none;
  background-color: white;
  margin: 0 8px;
}
.cart-item-row > div:first-child button > img {
  width: 100%;
  object-fit: cover;
}
.cart-item-row > div:first-child button:hover {
  box-shadow: 3px 3px rgb(255, 252, 99);
}
.cart-item-row > div:nth-child(3) {
  width: 10%;
  text-align: center;
}
.qtybtncol {
  width: 6%;
  text-align: center;
}
.qtybtncol button {
  padding: 0 5px;
  color: white;
  border-radius: 50%;
  font-weight: 900;
  text-align: center;
  line-height: 20px;
  border: none;
  width: 20px;
  height: 20px;
}
.qtybtncol button:hover {
  border: 1.5px solid yellow;
}
.deductQtyBtn {
  background-color: rgb(255, 188, 73);
}
.addQtyBtn {
  background-color: rgb(66, 212, 53);
}
.cart-item-row > div:nth-child(5) {
  width: 13%;
  text-align: right;
}
.cart-item-row > div:nth-child(6) {
  width: 15%;
  text-align: right;
}
.cart-item-row > div:last-child {
  width: 10%;
  text-align: right;
}
.cart-item-row > div:last-child button {
  background-color: rgb(255, 115, 0);
}
.total-cost-div {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-content: flex-end;
  border-top: 1px solid rgb(66, 212, 53);
  margin: 5px 0;
  padding: 2px 15px;
}
.total-cost-div div {
  font-size: 16px;
  margin-top: 5px;
  font-weight: 700;
  color: rgb(255, 162, 0);
}
.total-cost-div div:last-child {
  margin-right: 15px;
}
.cart-table > div:last-child {
  width: 100%;
}
.clear-cart-div {
  text-align: center;
  margin-top: 10px;
  display: flex;
  justify-content: center;
}
.clear-cart-div button {
  width: 200px;
  height: 34px;
  padding: 5px 0;
  background-color: rgb(255, 162, 0);
  color: white;
  border: none;
  border-radius: 10px;
}
.clear-cart-div button:hover {
  background-color: rgb(255, 115, 0);
}
.own-pizza-clear {
  text-align: center;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.own-pizza-clear button {
  width: 120px;
  height: 34px;
  padding: 5px 0;
  background-color: rgb(255, 162, 0);
  color: white;
  border: none;
  border-radius: 10px;
}
.own-pizza-clear button:hover {
  background-color: rgb(255, 115, 0);
}
.own-pizza-clear button:disabled {
  background-color: rgb(218, 218, 218);
  color: rgb(100, 100, 100);
}
.no-item {
  width: 100%;
  text-align: center;
  font-size: 12px;
  margin: 15px 0;
}
.quantity {
  width: 40px;
  margin: 5px 5px 0 0;
  text-align: center;
}
.heading div {
  font-weight: 700;
  padding-bottom: 5px;
  color: rgb(255, 162, 0);
  font-size: 16px;
  text-align: center;
}
.heading {
  border-bottom: 1px solid rgb(255, 162, 0);
  margin-bottom: 5px;
}

/* Create and Edit Boxes */

.create-edit-box {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}
.create-edit-box > div:first-child > button {
  width: 40px;
  height: 40px;
  border: none;
  margin-bottom: 10px;
  background-color: white;
  overflow: hidden;
  border-radius: 50%;
  border: 4px solid white;
}
.create-edit-box > div:first-child > button img {
  width: 100%;
}
.crud-menu-item-box {
  width: 360px;
  padding: 20px 10px 5px 10px;
  background-color: rgb(255, 255, 255);
  border-radius: 15px;
  border: 3px solid rgb(228, 228, 228);
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}
.crud-menu-item-box h4 {
  width: 100%;
  text-align: center;
  font-size: 18px;
  color: rgb(66, 212, 53);
  border-bottom: 1px solid rgb(151, 231, 143);
  padding: 0 10px 10px 10px;
}
.crud-menu-item-box > div:nth-child(2) {
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid rgb(151, 231, 143);
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}
.crud-menu-item-box > div:nth-child(2) > div {
  width: 100%;
  margin-bottom: 8px;
}
.crud-menu-item-box > div:nth-child(2) > div {
  font-size: 12px;
  font-weight: 600;
  color: rgb(255, 115, 0);
  margin-bottom: 5px;
}
.crud-menu-item-box > div:nth-child(2) > div > div {
  margin-bottom: 5px;
}
.crud-menu-item-box > div:nth-child(2) > div:nth-child(5) {
  text-align: center;
}
.crud-menu-item-box > div:nth-child(2) > div > input,
.crud-menu-item-box > div:nth-child(2) > div > select {
  width: 100%;
  padding: 4px 5px;
  margin-bottom: 5px;
  border: 1px solid rgb(66, 212, 53);
  outline: rgb(66, 212, 53);
}
.crud-menu-item-box > div:nth-child(3) {
  width: 100%;
}
.crud-menu-item-box > div:nth-child(2) > div:last-child {
  display: flex;
  justify-content: space-between;
}
.crud-menu-item-box > div:nth-child(2) button {
  margin-top: 5px;
  padding: 5px 10px;
  border-radius: 3px;
  border: 0;
  background-color: rgb(66, 212, 53);
  color: white;
}
.create-menu-item button {
  width: 100%;
}
.edit-menu-item button {
  width: 48%;
}
.crud-menu-item-box > div:nth-child(2) button:hover {
  background-color: rgb(22, 179, 8);
}
.crud-menu-item-box > div:nth-child(2) button:active {
  background-color: rgb(12, 139, 0);
}

/* Own Pizza Box */

.own-pizza-box > div:nth-child(3) {
  width: 100%;
}
.ing-choices {
  margin-top: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.ing-choices h3 {
  width: 100%;
}
